/* eslint-disable no-unused-vars */
import React, { useEffect, useState } from 'react';
import { connect, useDispatch, useSelector } from 'react-redux';
import { bindActionCreators } from 'redux';
import { MODAL_TYPE } from 'src/constants';
import { addDeliveryToCart } from 'src/redux/actions/cart.action';
import { addModal } from 'src/redux/actions/ui.action';
import { useModal } from 'src/hooks';

import {
  getListVoucherByMerchantHandle,
  getVoucherByIdHandle,
} from 'src/redux/actions/voucher.action';
import { getAccessTokenSelector } from 'src/redux/selectors/auth';
import DeliveryDetailView from './deliveryDetail';

const DeliveryDetailContainer = (props) => {
  const [voucher, setVoucher] = useState({});
  const [suggestVoucher, setSuggestVoucher] = useState([]);
  const [quantity, setQuantity] = useState(1);
  const dispatch = useDispatch();
  const accessToken = useSelector(getAccessTokenSelector);
  const { Modal, show, hide } = useModal();
  useEffect(() => {
    if (props?.match?.params?.id) {
      props.getVoucherByIdHandle(props?.match?.params?.id, (voucherDetail) => {
        setVoucher(voucherDetail);
      });
    }
  }, [props?.match?.params?.id]);
  useEffect(() => {
    if (voucher?.merchant?.id) {
      dispatch(
        getListVoucherByMerchantHandle(
          1,
          5,
          {
            merchant_id: voucher?.merchant?.id,
          },
          (listSuggestVoucher) => {
            setSuggestVoucher(listSuggestVoucher);
          }
        )
      );
    }
  }, [voucher]);
  const onAddItemToCartClick = () => {
    if (accessToken) {
      dispatch(addDeliveryToCart({ ...voucher, quantity }, voucher.merchant));
      dispatch(
        addModal({
          type: MODAL_TYPE.ALERT_SUCCESS,
          data: {
            title: 'Thông báo',
            subTitle: 'Đã thêm vào giỏ hàng thành công',
            titleButton: 'Hoàn tất',
          },
        })
      );
    } else {
      dispatch(addModal({ type: MODAL_TYPE.FORCE_USER_LOGIN }));
    }
  };
  return (
    <DeliveryDetailView
      suggestVoucher={suggestVoucher}
      onAddItemToCartClick={onAddItemToCartClick}
      voucher={voucher}
      onQuantityChange={(value) => {
        setQuantity(value);
      }}
      Modal={Modal}
      hide={hide}
      quantity={quantity}
    />
  );
};

const mapStateToProps = () => ({});
const mapDispatchToProps = (dispatch) =>
  bindActionCreators({ getVoucherByIdHandle }, dispatch);
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DeliveryDetailContainer);
