import {
  ADD_DELIVERY_TO_CART,
  ADD_VOUCHER_TO_CART,
  UPDATE_LIST_DELIVERY_IN_CART,
  UPDATE_LIST_VOUCHER_IN_CART,
} from '../constants/cart.constant';

export const addVoucherToCart = (voucher) => ({
  type: ADD_VOUCHER_TO_CART.HANDLER,
  voucher,
});

export const updateListVoucherInCart = (vouchers) => ({
  type: UPDATE_LIST_VOUCHER_IN_CART.HANDLER,
  vouchers,
});

export const addDeliveryToCart = (food, merchant) => ({
  type: ADD_DELIVERY_TO_CART.HANDLER,
  food,
  merchant,
});

export const updateListDeliveryInCart = (merchant, foods) => ({
  type: UPDATE_LIST_DELIVERY_IN_CART.HANDLER,
  merchant,
  foods,
});
